import Vue from 'vue';
import VueRouter from 'vue-router';
import Merge from '../views/Merge.vue';

Vue.use(VueRouter);

const routes = [
	{
		path: '/',
		name: 'Merge',
		component: Merge,
	},
	{
		path: '/grid',
		name: 'Grid',
		component: () => import('../views/Grid.vue'),
	},
];

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes,
});

export default router;
